<template>
  <v-panel
    title="Transfer"
    label="Transactions History"
    double="true"
    secondPrimary="true"
    :loading="loading"
  >
    <template #form-wrap>
      <Form
        :title="'Amount in ' + currency"
        subtitle="1% comission"
        type="text"
        value="0.01"
        v-model="amount"
      />
      <Form
        title="Select currency"
        type="select"
        selectWidth="100%"
        :options="wallets.map(wallet => wallet.currency.capitalize())"
      />
      <Form
        title="Address to transfer"
        type="text"
        value=""
        isAddress="true"
        v-model="address"
      />
      <Button
        class="pay_button"
        theme="secondary_big"
        text="Transfer"
        @click.native="transfer"
      />
    </template>
    <template #second-form-wrap>
      <NoData
        :loading="false"
        LT="Your transactions are loading..."
        ET="There is nothing to list"
      />
    </template>
  </v-panel>
</template>

<script>
import NoData from '../../../partial/NoData'
import Button from '../Button.vue'
import Form from '../Form.vue'
import { mapGetters } from 'vuex'

export default {
  name: 'Transfer',
  components: {
    Form,
    Button,
    NoData,
  },
  data: () => ({
    address: '',
    amount: '0.1',
    loading: false,
  }),
  computed: {
    ...mapGetters({
      wallets: 'auth/profileWallets',
      currency: 'popups/exchangeSelectedCurrency',
    }),
  },
  methods: {
    transfer() {
      let amount = +this.amount

      if (isNaN(amount)) {
        this.handleError({
          message: 'Amount must be a number!',
        })
      } else {
        this.loading = true
        this.$axios
          .post('/api/wallet/transfer', {
            recipient: this.address,
            amount,
            currency: this.currency,
          })
          .then(response => {
            console.log(response)
            this.$axios
              .get('/api/wallet')
              .then(({ data: wallets }) => {
                this.$store.commit('auth/TRANSFER', wallets)

                this.loading = false
                this.handleSuccess(response.data)
              })
              .catch(err => {
                this.loading = false
                console.log(err)
              })
          })
          .catch(err => {
            this.loading = false
            this.handleError(err.response.data)
          })
      }
    },
    handleError(response) {
      console.log(response)
      this.$store.commit('popups/ADD_ALERT', {
        type: 'error',
        title: 'Transfer failed',
        message: response.message,
      })
    },
    handleSuccess(response) {
      console.log(response)
      this.$store.commit('popups/ADD_ALERT', {
        type: 'message',
        title: 'Transfer succeed!',
        message:
          'You have just successfully sent ' +
          response.amount +
          ' of ' +
          this.currency +
          '!',
      })
    },
  },
}
</script>

<style lang="sass" scoped>
@import "../../../../scss/smart-grid"
@import "../../../../scss/variables"

.pay_button
  margin-top: 10px
  width: calc(100% - 38px)
</style>
